
.register {
  position: fixed;
  top: 45px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: #478aff;
  overflow-x: hidden;
  text-align: center;
}
.register .login-bg {
  display: none;
  position: fixed;
  top: 3em;
  left: 0;
  z-index: -1;
  background: #478aff;
  width: 100%;
  height: 580px;
}
.register .login-bg img {
  height: 580px;
}
.register .dialog-wrapper {
  display: inline-block;
  width: 500px;
  padding: 27px 45px 40px;
  background: #fff;
  border-radius: 4px;
  margin-top: 5%;
}
.register .dialog-wrapper .login-dialog {
  padding: 2em 1em;
  background: #fff;
  border: 1px solid #ccc;
}
.register .dialog-wrapper .login-dialog .login-item {
  margin: 0.5em;
}
.register .dialog-wrapper .login-dialog .login-item input {
  width: 100%;
}
.register .dialog-wrapper .login-dialog .login-item button {
  width: 100%;
  display: inline-block;
}


