
.user-profile {
  min-width: 500px;
  margin: 0 auto;
  text-align: center;
}
.user-profile .tab-list {
  display: inline-block;
  padding-bottom: 10px;
}
.user-profile .tab-list .tab-item {
  cursor: pointer;
}
.user-profile .tab-show {
  border-top: 1px solid #ccc;
  padding-top: 10px;
}
.user-profile .tab-show .tab-show-item label,
.user-profile .tab-show .tab-show-item input,
.user-profile .tab-show .tab-show-item span {
  display: inline-block;
}
.user-profile .tab-show .tab-show-item label {
  text-align: left;
  width: 5em;
}
.user-profile .tab-show .tab-show-item .item-text {
  text-align: left;
  width: 50%;
  height: 33px;
  padding: 6px 12px;
  margin-left: 10px;
  border-bottom: 1px solid #ccc;
}
.user-profile .tab-show .tab-show-item .form-control {
  width: 50%;
  margin-left: 10px;
}


