
#app {
  text-align: center;
}
#app .nav-bar {
  z-index: 10;
  width: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  height: 45px;
  background-color: #f3f5f7;
  text-align: center;
}
#app .nav-bar .logo {
  display: inline-block;
  margin-left: 1em;
  margin-right: 1em;
  height: 45px;
  vertical-align: top;
}
#app .nav-bar .logo img {
  height: 100%;
}
#app .nav-bar .account {
  position: fixed;
  right: 0;
  top: 0;
  margin-right: 1em;
  height: 45px;
  line-height: 45px;
}
#app .nav-bar .account .account-item {
  cursor: pointer;
}
#app .nav-bar .user {
  position: fixed;
  right: 0;
  top: 0;
  margin-right: 1em;
  height: 45px;
  line-height: 45px;
}
#app .nav-bar .user .user-item {
  cursor: pointer;
  color: #0f88eb;
}
#app .nav-bar .nav-item {
  height: 45px;
  line-height: 45px;
  display: inline-block;
  vertical-align: top;
}
#app .nav-bar .nav-item .nav-bar-item {
  outline: none;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  margin-left: 1em;
  margin-right: 1em;
  color: #777;
}
#app .nav-bar .nav-item .nav-bar-item.router-link-active {
  color: #478aff;
  font-weight: bold;
}
#app .main-content {
  margin-top: 45px;
  display: inline-block;
  text-align: left;
}
#app .foot {
  clear: both;
}


