
.please-login {
  position: fixed;
  top: 45px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: #478aff;
  overflow-x: hidden;
  text-align: center;
}
.please-login .login-bg {
  display: none;
  position: fixed;
  top: 3em;
  left: 0;
  z-index: -1;
  background: #478aff;
  width: 100%;
  height: 580px;
}
.please-login .login-bg img {
  height: 580px;
}
.please-login .dialog-wrapper {
  display: inline-block;
  width: 500px;
  padding: 27px 45px 40px;
  background: #fff;
  border-radius: 4px;
  margin-top: 5%;
}
.please-login .dialog-wrapper .login-dialog {
  padding: 2em 1em;
  background: #fff;
  border: 1px solid #ccc;
}
.please-login .dialog-wrapper .login-dialog .login-item {
  margin: 0.5em;
  text-align: left;
}
.please-login .dialog-wrapper .login-dialog .login-item #imageCode {
  width: 40%;
  display: inline-block;
}
.please-login .dialog-wrapper .login-dialog .login-item .imageCode {
  float: right;
  width: 30%;
  display: inline-block;
  height: 30px;
}
.please-login .dialog-wrapper .login-dialog .login-item .changeImageCode {
  float: right;
  height: 30px;
  line-height: 30px;
  cursor: pointer;
}
.please-login .dialog-wrapper .login-dialog .login-item input {
  width: 100%;
}
.please-login .dialog-wrapper .login-dialog .login-item button {
  width: 100%;
  display: inline-block;
}


