
.simulation .simulation-list .simulation-item {
  width: 700px;
  height: 50px;
  margin: 10px;
  display: block;
  border: 1px solid #ccc;
  -webkit-box-shadow: 3px 3px 2px #dfdbda;
          box-shadow: 3px 3px 2px #dfdbda;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.simulation .simulation-list .simulation-item:hover {
  -webkit-box-shadow: 3px 3px 2px #ccc;
          box-shadow: 3px 3px 2px #ccc;
}
.simulation .simulation-list .simulation-item .title {
  font-weight: bold;
  margin-left: 50px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
  vertical-align: top;
}
.simulation .simulation-list .simulation-item .link {
  margin-right: 30px;
  float: right;
  height: 50px;
  line-height: 50px;
  vertical-align: top;
}


