
.video-page .player {
  width: 800px;
  clear: both;
}
.video-page .player video {
  width: 100%;
}
.video-page .player video::-webkit-media-controls-enclosure {
  overflow: hidden;
}
.video-page .player video::-webkit-media-controls-panel {
  width: calc(100% + 30px);
}


