
.home .wrapper {
  position: relative;
  overflow: hidden;
}
.home .wrapper:hover .wrapper-arrow {
  display: block;
  background-color: rgba(0,0,0,0.7);
}
.home .wrapper .wrapper-content {
  position: absolute;
  left: 0;
  z-index: 1;
}
.home .wrapper .wrapper-content .img-wrapper {
  border: none;
  outline: none;
  float: left;
  position: relative;
}
.home .wrapper .wrapper-content .img-wrapper .img-text {
  color: #fff;
  display: inline-block;
  width: 50%;
  text-align: center;
  position: absolute;
  top: 30%;
  right: 0;
}
.home .wrapper .wrapper-content .img-wrapper .img-text .img-text-title {
  font-size: 3em;
  display: block;
}
.home .wrapper .wrapper-content .img-wrapper .img-text .img-text-content {
  font-size: 2.5em;
}
.home .wrapper .wrapper-content .img-wrapper .wrapper-content_img {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
}
.home .wrapper .wrapper-buttons {
  position: absolute;
  width: 100px;
  height: 20px;
  text-align: center;
  bottom: 3px;
  z-index: 2;
}
.home .wrapper .wrapper-buttons .wrapper-button {
  float: left;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #fff;
  border: 1px solid #337ab7;
  margin: 0 5px;
  cursor: pointer;
}
.home .wrapper .wrapper-buttons .wrapper_on {
  background-color: #337ab7;
}
.home .wrapper .wrapper-arrow {
  position: absolute;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background-color: rgba(0,0,0,0.3);
  color: #fff;
  display: none;
  top: 50%;
  line-height: 40px;
  font-size: 36px;
  text-align: center;
  z-index: 2;
}
.home .wrapper .wrapper-prev {
  left: 10px;
}
.home .wrapper .wrapper-next {
  right: 10px;
}
.home .wrapper .wrapper_trans {
  -webkit-transition: left 0.3s ease;
  transition: left 0.3s ease;
}
.home .introduce h1 {
  color: #337ab7;
  font-size: 50px;
  font-weight: bold;
}
.home .introduce .desc {
  font-size: 20px;
  font-weight: bold;
  margin-left: 20px;
}
.home .bgc .card-wrapper {
  text-align: center;
}
.home .bgc .card-wrapper .card {
  width: 210px;
  height: 270px;
  margin-right: 1%;
  margin-bottom: 10px;
  display: inline-block;
  border: 1px solid #ccc;
  vertical-align: top;
  -webkit-box-shadow: 6px 6px 2px #dfdbda;
          box-shadow: 6px 6px 2px #dfdbda;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.home .bgc .card-wrapper .card:hover {
  background: #337ab7;
}
.home .bgc .card-wrapper .card:hover .title {
  color: #fff;
}
.home .bgc .card-wrapper .card:hover .content {
  color: #fff;
}
.home .bgc .card-wrapper .card .icon {
  width: 88px;
  height: 88px;
  margin: 20px auto;
}
.home .bgc .card-wrapper .card .icon img {
  width: 100%;
  height: 100%;
}
.home .bgc .card-wrapper .card .title {
  color: #000;
  font-size: 17px;
  font-weight: bold;
}
.home .bgc .card-wrapper .card .content {
  font-size: 15px;
  color: #777;
  width: 60%;
  margin: 10px auto;
}


