
.free-course .category-list {
  width: 100%;
  border-bottom: 1px solid #2e6da4;
}
.free-course .category-list .category-title {
  font-weight: bold;
}
.free-course .category-list .category-item {
  cursor: pointer;
  display: inline-block;
  padding: 0 10px;
  margin: 10px 4px;
  height: 34px;
  line-height: 34px;
  border-radius: 2px;
}
.free-course .category-list .category-item:hover {
  color: #2e6da4;
}
.free-course .category-list .category-item.selected {
  color: #fff;
  font-weight: bold;
  background: #2e6da4;
}
.free-course .course-list {
/*text-align center*/
  width: 1120px;
  margin: 0 auto;
}
.free-course .course-list .course-item {
/*display inline-block*/
  float: left;
  width: 240px;
  margin: 20px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  cursor: pointer;
  border: 1px solid #ccc;
  -webkit-box-shadow: 3px 3px 2px #dfdbda;
          box-shadow: 3px 3px 2px #dfdbda;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.free-course .course-list .course-item:hover {
  -webkit-box-shadow: 3px 3px 2px #777;
          box-shadow: 3px 3px 2px #777;
}
.free-course .course-list .course-item .img {
  width: 100%;
}
.free-course .course-list .course-item .img img {
  width: 100%;
  border-radius: 10px;
}
.free-course .course-list .course-item .title {
  text-align: center;
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: bold;
}


